import style from "../LmsManager.module.css";
import { useTranslation } from "react-i18next";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import LmsRecommendationAuthorityStaffCardMenu from "./LmsRecommendationAuthorityStaffCardMenu";
import { useState } from "react";
const LmsRecommendationAuthorityStaffCard = ({
  staff,
  onRefetch,
  modId,
  flow,
}) => {
  const { t } = useTranslation();
  const [menu, setMenu] = useState(false);

  const onToggle = () => {
    setMenu((pre) => !pre);
  };
  return (
    <>
      <div className={style.lms_staff_card}>
        <div className={style.lms_staff_card_contatiner}>
          <img
            src={
              staff?.staffProfilePhoto
                ? `${imageShowUrl}/${staff?.staffProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
            alt="avatar"
          />

          <div className={style.lms_staff_card_contatiner_inner}>
            <h6>
              {`${staff?.staffFirstName ?? ""} ${
                staff?.staffMiddleName ?? ""
              } ${staff?.staffLastName ?? ""}`}
            </h6>
            <p>
              {t("index_number")}
              {": "}
              {staff?.staffROLLNO ?? ""}
            </p>
          </div>
        </div>
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onToggle}
          title="Menu"
          style={{
            cursor: "pointer",
            height: "1.5rem",
          }}
        />
      </div>
      {menu && (
        <LmsRecommendationAuthorityStaffCardMenu
          onClose={onToggle}
          onRefetch={onRefetch}
          staff={staff}
          flow={flow}
          modId={modId}
        />
      )}
    </>
  );
};

export default LmsRecommendationAuthorityStaffCard;
