import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useTranslation } from "react-i18next";
import { useState } from "react";
import { useLmsModeratorRemoveStaff } from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";

const LmsRecommendationAuthorityStaffCardMenu = ({
  onClose,
  modId,
  onRefetch,
  flow,
  staff,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [lmsModeratorRemoveStaff] = useLmsModeratorRemoveStaff();

  const onDelete = () => {
    if (staff?._id && modId && flow) {
      setDisabled((pre) => !pre);
      lmsModeratorRemoveStaff({
        mid: modId,
        removeStaff: {
          flow: flow,
          staff_arr: [staff?._id],
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <>
      <ModalMenu onClose={onClose} disabled={disabled}>
        <h6 onClick={onDelete}>{t("remove")}</h6>
      </ModalMenu>
    </>
  );
};

export default LmsRecommendationAuthorityStaffCardMenu;
